
<template>
  <center>
    <iframe title="YouTube video player" class="youtube-player" type="text/html" width="320" height="180" :src=live.vu frameborder="0" allowFullScreen></iframe>
    <a :href=appurl>{{live.ti}}</a>
    <a
  </center>
</template>

<script>
import Vue from 'vue';
import Amplify, { API, Analytics } from 'aws-amplify';

export default {
  name: 'GacLive',

  data () {
    return {
      liveId: '',
      live: {},
      appurl: '' 
    }
  },

  async created() {
    this.liveId = this.$route.params.liveid;
    API.post('gacLiveInfo', '', {body: {'body': JSON.stringify({'AV':0, 'li':this.liveId})}}).then(response => {
      console.log('gacLiveInfo return : %o',  response);
      this.live = JSON.parse(response.body).Live;
      this.live.vu = this.live.vu.replace('watch?v=', 'embed/');
      this.appurl = "com.wjf.gac://live?id=" + this.liveId;
    }).catch(error => {
      console.log(error);
    });
  }
}

</script>



