
<template>
  <div><v-menu/>
  <center>
  <h1>{{message}}</h1>

  <p class="question" v-html=q></p>
  <div class="support" v-show="answered"><p>{{cs[a]}}</p><p>{{s}}</p>
    <p> <button class="next" v-on:click="loadQuiz()">Next quiz</button> </p>
  </div>
  <div class=choices v-for="(c, index) in cs" :key="index" :class="{'bounce animated': animated}">
    <button v-on:click="choice(index)" v-bind:class="{ 'greenChoice': isGreen(index), 'redChoice': isRed(index), 'blackChoice': isBlack(index)}">{{c}}</button>
  </div>

  <p>400,000+ trivia quizzes</p>
  </center>
  <v-foot/></div>
</template>

<script>
import Vue from 'vue';
import Menu from '@/components/Menu';
Vue.component('v-menu', Menu)
import Foot from '@/components/Foot';
Vue.component('v-foot', Foot)

import Amplify, { API, Analytics } from 'aws-amplify';
import {replaceImg, shuffle} from '../quiz-funcs.js';

export default {
  name: 'GKTrivia',

  data () {
    return {
      quizname: 'General Knownledge Trivia',
      libprefixes : ['k'],
      libcounts: [413654],
      libnames: ['General'],
      s3bucket: 'gktrivia',

      message: 'Loading data, please wait seconds',
      libidx: 0,
      q: "",
      cs: [],
      a: 0,
      s: "",
      answered : false,
      choiced: -1,
      animated:false
    }
  },

  async created() {
    //const currentUser = await this.$Amplify.Auth.currentUserInfo();
    Analytics.record({ name: 'webGKTrivia' });
    this.loadQuiz();
  },


  methods: {
    animate: function() {
      var self = this;
      self.animated = true;
      setTimeout(function(){ self.animated = false; }, 1000);
    },

    choice: function(index) {
      if (this.answered) {
        this.animate();        
        return;
      }

      this.choiced = index
      this.answered = true;
    },

    switchLib: function(lib) {
      this.libidx = lib;
      this.loadQuiz();
    },

    loadQuiz: function() {
      var i = this.libprefixes[this.libidx] + (1+Math.floor(Math.random() * this.libcounts[this.libidx]));
      console.log('id: ' + i);

      API.post('quiz', '', {body: {'body': JSON.stringify({'i': i})}}).then(response => {
        this.message = this.quizname;
        var quiz = JSON.parse(response.body).Quiz;
        this.q = replaceImg(quiz.q, this.s3bucket);
        
        var a = quiz.a;
        var cs;
        if (quiz.d.length > 0) {
          cs = JSON.parse(quiz.d);
          cs.push(a);
          shuffle(cs);
        } else if (quiz.o.length > 0) {
          cs = JSON.parse(quiz.o);
        } else {
          cs = [];
        }

        var index = 0;
        for (var index=0; index<cs.length; index++) {
          var c = cs[index];
          if (a == c) {
            this.a = index;
          }
        }

        this.cs = cs;
        this.s = quiz.s;
        this.answered = false;
        window.scrollTo(0,0);
      }).catch(error => {
        this.message = 'Somethin went wrong, please retry later';
        console.log(error)
      });
    },

    isGreen: function(index) {
      return this.answered && index == this.a;
    },
    isRed: function(index) {
      return this.answered && this.choiced != this.a && this.choiced == index;
    },
    isBlack: function(index) {
      return !this.answered || this.choiced != index && this.a != index;
    }
  }
}

</script>



